:root {
	--nav-font-color: white;
	--nav-title-size: 2rem;
	--nav-title-weight: 600;
	--nav-font-size: 1.1rem;
	--nav-font-weight: 600;
	--nav-icon-size: 1.3rem;
	--nav-bg-color: #282c34;
	/* --body-bg-color: #F5F5F5; */
	--body-bg-color: #F5F5F0;
	/* --body-bg-color: #F3F3F3; */
	/* --body-bg-color: #e1e1e1; */
	--body-bg-color-accent: #708090;
	--body-font-color: black;
	--body-font-size: 1.2rem;
	--body-width: 720px;
}

* {
}

body {
	margin: 0;
	padding: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
	'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
	sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	font-size: var(--body-font-size);
	background-color: var(--body-bg-color);
	padding-bottom: 100px;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
	monospace;
}

nav {
	background-color: #282c34;
	height: min-content;
	color: white;

}

.nav-container {
	width: max-content;
	padding: 2rem;
	margin: 0 auto;

}

.nav--top-section {
	font-size: var(--nav-title-size);
	font-weight: var(--nav-title-weight);

}

.webname, .nav--button {
	color: white;
	text-decoration: none;
}

.nav--bottom-section {
	font-size: var(--nav-font-size);
	font-weight: var(--nav-font-weight);
	padding: 10px 0 0 0;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: flex-start;
}

.nav--icons {
	display: flex;
	align-items: center;
}

.nav--button-icon {
	margin: 0 0 0 15px;
	width: var(--nav-icon-size);
}

.content-container {
	margin: auto;
	padding: 20px;
	max-width: var(--body-width);
	line-height: 1.6;
	text-align: justify;
}

p {
	padding: 0;
	margin: 0;
}

.Projects {
	/* margin: auto; */
	display: flex;
}

.cards-list {
	margin: auto;
    display: flex;
	/* flex-wrap: wrap; */
	flex-direction: column;
    /* flex-wrap: nowrap; */
    /* gap: 20px; */
    /* overflow-x: auto; */
	justify-content: flex-start;
}

.card {
	background-color: var(--body-bg-color-accent);
	margin: 20px;
    max-width: 1000px;
	height: 400px;
    font-size: 1.3rem;
    display: flex;
    flex-direction: row;
	color: white;
	border-radius: 25px;
}

.card--image {
	border-radius: 25px 0 0 25px;
}

.card--content-section {
	display: flex;
	flex-direction: column;
	width: 100%;
}
.card--top-section {
	padding: 10px;
}

.card--title {
	font-size: 1.8rem;
	font-weight: 500;
}

.card--discription {
	font-size: 1rem;
}

.card--bottom-section {
	display: flex;
	flex-direction: column;
}

.card--tech {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: flex-start;
	gap: 10px;
	padding: 10px;
}

.card--tech-item {
	background-color: black;
	padding: 10px;
	border-radius: 8px;
}

.card--links {
	display: flex;
	justify-content: space-evenly;
	padding: 10px;
	gap: 20px;
}

.card--icon {
	background-color: white;
	padding: 8px;
	border-radius: 10px;
	width: 3rem;
}

